import Vue from 'vue';
import Router from 'vue-router';

import Welcome from '../views/welcome';
import PageList from '../views/page-list';
import EditPageV2 from '../views/edit-page-v2';
import Login from '../views/login';
import RichTextList from '../views/rich-text-list';
import ckeditor from '../views/ckeditor';
import neditor from '../views/neditor';
import ue from '../views/ue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      name: 'Welcome',
      path: '/',
      component: Welcome
    },
    {
      name: 'Login',
      path: '/Login',
      component: Login
    },
    {
      name: 'EditPageV2',
      path: '/EditPageV2',
      component: EditPageV2
    },
    {
      name: 'PageList',
      path: '/PageList',
      component: PageList
    },
    {
      name: 'RichTextList',
      path: '/RichTextList',
      component: RichTextList
    },
    {
      name: 'ckeditor',
      path: '/ckeditor',
      component: ckeditor
    },
    {
      name: 'neditor',
      path: '/neditor',
      component: neditor
    },
    {
      name: 'ue',
      path: '/ue',
      component: ue
    }
  ]
});



